<template>
  <v-row class="justify-center mt-6 pr-8 pl-8">
    <v-col lg="4" md="6" sm="7">
      <div class="mb-6 login_logo">
        <v-img
          max-height="250"
          max-width="100%"
          src="@/assets/logo.svg"
          contain
          class="mb-5 login_logo_img"
        ></v-img>
      </div>
      <v-form ref="form" v-model="valid" class="login_form">
        <v-text-field
          v-model="email"
          :rules="emailRules"
          autocomplete="email"
          label="Email"
          required
          outlined
          clearable
          color="primary"
        ></v-text-field>

        <v-text-field
          v-model="password"
          :rules="passwordRules"
          autocomplete="current-password"
          label="Password"
          type="password"
          required
          outlined
          clearable
          color="primary"
        ></v-text-field>

        <v-checkbox
          v-model="checkbox"
          label="Auto Login"
          style="width: fit-content"
        ></v-checkbox>

        <v-dialog v-model="dialog" persistent max-width="290">
          <template v-slot:activator="{ on }">
            <v-btn
              :disabled="!valid"
              color="blue darken-4"
              @click="validate"
              x-large
              block
              class="white--text"
            >
              Login
            </v-btn>
          </template>
          <v-card>
            <v-card-title class="text-h5"> Incorrect </v-card-title>
            <v-card-text>Please check your Login/Password</v-card-text>
            <v-card-actions>
              <v-btn
                color="blue darken-4"
                class="white--text"
                text
                @click="dialog = false"
              >
                Confirm
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-form>
    </v-col>
  </v-row>
</template>

<script>
export default {
  name: "Login",
  data: () => ({
    valid: true,
    password: "",
    passwordRules: [
      (v) => !!v || "password is required",
      (v) => (v && v.length >= 6) || "password must be more than 5 characters",
    ],
    email: "",
    emailRules: [
      (v) => !!v || "E-mail is required",
      (v) => /.+@.+\..+/.test(v) || "E-mail must be valid",
    ],
    checkbox: false,
    dialog: false,
  }),

  methods: {
    validate() {
      this.$refs.form.validate();
      if (this.email === "suorgx@aiaracorp.com") {
        this.$store.commit("changeAuth");
      } else {
        this.dialog = true;
      }
    },
  },
};
</script>

<style>
.login_logo {
  border-radius: 12px;
  background: #0d47a1;
  padding: 30px 30px 14px;
}
.login_logo_img {
  border-radius: 12px;
  background: #fff;
  padding: 30px 0;
}
.login_logo_img div {
  background-size: 80%;
}
.login_form .v-input__control {
  flex-direction: column !important;
}
.login_form .theme--light.v-input input,
.login_form .theme--light.v-input textarea {
  color: #222 !important;
}
</style>
