        <v-list-item-title
          class="text-subtitle-2 font-weight-bold white--text"
          v-text="item.title"
        ></v-list-item-title>
      </template>
      <v-list-item
        v-for="child in item.links"
        :key="child.title"
        style="background: #fff"
        class="list_group"
        @click="changeActive($event), $router.push(child.url)"
