  methods: {
    changeActive(event) {
      let titleItem = document.querySelectorAll(".item_title");
      titleItem.forEach((el) => el.classList.remove("item_title_active"));
      if (event.target.classList.contains("item_title")) {
        event.target.classList.add("item_title_active");
      } else {
        if (event.target.querySelector(".item_title")) {
          event.target
            .querySelector(".item_title")
            .classList.add("item_title_active");
        }
      }
    },
  },
